@import "../../config";
.NavBarItems {
  position: fixed;
  top: 0;
  height: 4rem;
  width: 100%;
  display: flex;
  background-color: rgba($background-color, 0.9);
  align-items: center;
  font-size: 1.2rem;
  transition: all 0.2s ease-in;

  &.transparent {
    background-color: rgba($background-color, 0);
  }
}
.navbar-logo {
  color: #fff;
  cursor: pointer;
  text-decoration: none;
  font-size: 1.8rem;
  margin-left: 20px;
}

.navbar-logo:hover {
  opacity: 0.5;
}

.fa-react {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  gap: 5px;
  list-style: none;
  text-align: center;
  margin-left: 20px;
}

.account-status {
  position: relative;
  margin-left: auto;
  margin-right: 1.5rem;
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: 10px;
}

.nav-links {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
  opacity: 0.65;
}

.nav-links:hover {
  transition: all 0.2s ease-out;
  opacity: 1;
}

.fa-bars {
  color: #fff;
}

.nav-links-sign-up {
  display: none;
}

.menu-icon {
  display: none;
}

.fa-times.close-drawer {
  display: none;
}

.nav-links.sign-in {
  display: none;
}

@media screen and (max-width: 900px) {
  .NavBarItems {
    justify-content: center;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    margin-left: 0;
    position: absolute;
    top: 0px;
    left: 100%;
    opacity: 1;
    transition: all 0.5s ease;

    &.active {
      display: flex;
      background: #1d1d24;
      left: 0%;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  }

  .nav-links {
    text-align: center;
    padding: 1rem;
    display: block;
    font-size: 1.4rem;

    &:hover {
      background-color: #7577fa;
      border-radius: 0;
    }
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 35%);
    font-size: 1.8rem;
    cursor: pointer;
    &:hover {
      opacity: 0.5;
    }
  }

  .fa-times.close-drawer {
    display: block;
    text-align: start;
    width: 2rem;
    margin-bottom: 1rem;
    position: relative;
    cursor: pointer;
    color: #fff;
    font-size: 2rem;
    padding: 2px;
    top: 1rem;
    left: 1rem;
    &:hover {
      opacity: 0.5;
    }
  }

  .nav-links.sign-in {
    display: block;
    text-align: center;
    padding: 0.8rem;
    margin: 1rem auto;
    border-radius: 5px;
    width: 80%;
    border: 2px white solid;
    background: transparent;
    text-decoration: none;
    color: #fff;
    font-size: 1.4rem;
  }

  .nav-links-sign-up {
    display: block;
    text-align: center;
    padding: 0.8rem;
    margin: auto;
    border-radius: 5px;
    width: 80%;
    background: #1e5ce3;
    text-decoration: none;
    color: #fff;
    font-size: 1.4rem;
    &:hover {
      background: #fff;
      color: #6568f4;
      transition: 250ms;
    }
  }

  .account-status {
    display: none;
  }
}
